.card-goal-col {
  padding-top: 25px;
}

.card-goal-col:first-child {
  padding-top: 0;
}

.goals-edit-event:hover {
  color: #45AAF2;
  transition: 0.5s;
}

.goals-delete-event:hover {
  color: #FC5C65;
  transition: 0.5s;
}

.ant-select-selection__placeholder {
  margin-left: 0 !important;
}

.goalSelectBy .ant-select-selection__choice__content .ant-tag {
  margin-right: 0;
  padding-right: 20px;
}

.goalSelectBy .ant-select-selection__choice {
  padding: 0 !important;
  border: none !important;
}

.goalSelectBy .ant-select-selection__choice__remove {
  margin-top: 0.5px;
  color: #383A3FE3 !important;
}

.input-number-goals .ant-input-number-handler-wrap {
  display: none;
}
