.ant-fullcalendar-content ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.ant-fullcalendar-content li {
    overflow: hidden;
    white-space: nowrap;
    width: 100%;
    text-overflow: ellipsis;
    font-size: 12px;
}

.calendar-edit-event:hover {
    color: #45AAF2;
    transition: 0.5s;
}

.calendar-delete-event:hover {
  color: #FC5C65;
  transition: 0.5s;
}

.ant-tag.ant-tag-checkable:not(.ant-tag-checkable-checked) {
    color: #383A3F !important;
    border-color: white !important;
    background: white !important;
}