.gradient {
  -webkit-animation-duration: 1.8s;
          animation-duration: 1.8s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: placeHolderShimmer;
          animation-name: placeHolderShimmer;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  background: -webkit-gradient(linear, left top, right top, from(#3867D6), color-stop(50%, #20BF6B), to(#3867D6));
  background: -webkit-linear-gradient(left, #3867D6 0%, #20BF6B 50%, #3867D6 100%);
  background: linear-gradient(to right, #3867D6 0%, #20BF6B 50%, #3867D6 100%);
  background-size: 1000px 640px;
  position: relative;
}

@-webkit-keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0
  }
  100% {
    background-position: 468px 0
  }
}

@keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0
  }
  100% {
    background-position: 468px 0
  }
}
