div.gradient-top {
  width: 100%;
  height: 5px;
  background-image: linear-gradient(121deg, #3867D6, #20BF6B);
}

header.haccoon-header {
  background: #FFF;
  z-index: 10;
  padding: 0;
  height: 55px;
  line-height: 50px;
  -webkit-box-shadow: 0 2px 8px #E0E0DF;
  box-shadow: 0 2px 8px #E0E0DF;
}

.menu-user {
  text-align: right;
}

.menu-user .ant-dropdown-trigger {
  cursor: pointer;
}

.ant-dropdown-menu-item a .anticon {
  margin-right: 8px;
}

@media only screen and (max-width: 576px) {
  .menu-user span.username, .menu-user .anticon {
    display: none !important;
  }

  .menu-user .ant-avatar {
    margin-right: 0 !important;
    height: 24px !important;
    width: 24px !important;
    margin-top: -4px !important;
  }

  .header-content div.text-center {
    text-align: left !important;
  }
}